.gameWrapper {
  font-family: 'Game';
  // background-image: url('menuBackground.png');
}

.canvas {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: 'Cyberpunk Is Not Dead', sans-serif;
  color: #ddd;
  overflow: hidden;

  canvas {
    border: none;
  }
}

.menu {
  padding-left: 50px;
  transform: translateX(-400px);
  height: 100%;
  width: 100%;
  display: flex;
  gap: 25px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: 150ms all ease-out;
  justify-content: center;
  align-items: baseline;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.loadingLabel {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 40px;
}

.soundIcon {
  margin-top: 30px;
  cursor: pointer;
}

.fullscreenWrapper {
  position: absolute;
  bottom: 10px;
  right: 10px;
  cursor: pointer;
}

.hiddenMenu {
  transition: all 100ms;
  pointer-events: none;
  backdrop-filter: blur(0);
  display: flex;
  justify-content: flex-end;

  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
}

.menuWrapper {
  display: flex;
  transition: all 100ms;
  // backdrop-filter: blur(3px);
  // background-color: rgba(0,0,0,0.4);

  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 1;
  overflow: hidden;

  

  .menu {
    transform: translateX(0);
  }
}

.menuBackground {
  background-size: cover;
  filter: blur(7px);
  box-shadow: 0 0 20px black;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.bottomBar {
  display: flex;
  gap: 20px;
  position: absolute;
  right: 80px;
  bottom: 40px;
}